body {
  margin: 0;
}

.stopwatch {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch__circle {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__center {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch__minutes {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite move-in-steps;
  position: absolute;
  top: 30%;
}

.stopwatch__seconds {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite move;
  position: absolute;
  top: 12%;
}

.stopwatch--speed-up .stopwatch__seconds {
  animation: 10s linear infinite move;
}

.stopwatch--speed-up .stopwatch__minutes {
  animation: 600s steps(60, end) infinite move-in-steps;
}

@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes move-in-steps {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
